import Vue from 'vue'
import VueRouter from 'vue-router'
import Footer from '@/components/Footer'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    components: {
      default: () => import('@/views/home/index.vue'),
      footer: Footer
    },
    meta: {
      keepAlive: true
    }
  },
  // {
  //   path: '/coupon',
  //   name: 'coupon',
  //   components: {
  //     default: () => import('@/views/coupon/index.vue'),
  //     footer: Footer
  //   }
  // },
  {
    path: '/recharge',
    name: 'recharge',
    components: {
      default: () => import('@/views/recharge/index.vue'),
      footer: Footer
    }
  },
  {
    path: '/kind',
    name: 'kind',
    components: {
      default: () => import('@/views/kind/index.vue'),
      footer: Footer
    }
  },
  // {
  //   path: '/coupon',
  //   name: 'coupon',
  //   components: {
  //     default: () => import('@/views/coupon/index.vue')
  //   }
  // },
  {
    path: '/detail/:proid',
    name: 'detail',
    components: {
      default: () => import('@/views/detail/index.vue')
    }
  },
  {
    path: '/coupon',
    name: 'coupon',
    components: {
      default: () => import('@/views/coupon/index.vue')
    }
  },
  {
    path: '/assemble',
    name: 'assemble',
    components: {
      default: () => import('@/views/assemble/index.vue'),
      footer: Footer
    }
  },
  {
    path: '/speli',
    name: 'speli',
    components: {
      default: () => import('@/components/Speli.vue')
    }
  },
  {
    path: '/store',
    name: 'store',
    components: {
      default: () => import('@/views/store/index.vue'),
      footer: Footer
    }
  },
  {
    path: '/user',
    name: 'user',
    components: {
      default: () => import('@/views/user/index.vue'),
      footer: Footer
    }
  },
  {
    path: '/detail',
    name: 'detail',
    components: {
      default: () => import('@/views/detail/index.vue')
    }
  },
  {
    path: '/updatas',
    name: 'updatas',
    components: {
      default: () => import('@/views/updatas/index.vue')
    }
  },
  {
    path: '/cart',
    name: 'cart',
    components: {
      default: () => import('@/views/cart/index.vue')
    }
  },
  {
    path: '/order',
    name: 'order',
    components: {
      default: () => import('@/views/order/index.vue')
    }
  },
  {
    path: '/address',
    name: 'address',
    components: {
      default: () => import('@/views/address/index.vue')
    }
  },
  {
    path: '/serch',
    name: 'serch',
    components: {
      default: () => import('@/views/serch/index.vue')
    }
  },
  {
    path: '/addAddress',
    name: 'addAddress',
    components: {
      default: () => import('@/views/address/add.vue')
    }
  },
  {
    path: '/updateAddress',
    name: 'updateAddress',
    components: {
      default: () => import('@/views/address/update.vue')
    }
  },
  {
    path: '/userorder',
    name: 'userorder',
    components: {
      default: () => import('@/views/userorder/index.vue'),
      footer: Footer
    }
  },
  {
    path: '/shouhuo',
    name: 'shouhuo',
    components: {
      default: () => import('@/views/shouhuo/index.vue'),
      footer: Footer
    }
  },
  {
    path: '/evaluate',
    name: 'evaluate',
    components: {
      default: () => import('@/views/evaluate/index.vue')
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/login/index.vue')
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('@/views/register/index.vue')
  },
  {
    path: '/detalis/:proid',
    name: 'detalis',
    component: () => import('@/views/assemble/index.vue')
  },
  {
    path: '/payment',
    name: 'payment',
    component: () => import('@/views/payment/index.vue')
  },
  {
    path: '**',
    name: '404',
    component: () => import('@/views/notfound/index.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
